.container-about {
    width: 100vw;
    height: 90vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

.blob-container {
    position: relative;
    overflow: hidden;
    height: 800px;
    width: 600px;
}

.blob-text {
    font-size: 20px;
    position: absolute;
    display: inline-block;
    margin: auto;
    width: 350px;
    color: #B8F4BB;
    z-index: 2;
}