.contact-icon-lin {
    background-color: #0EA86C;
    color: #B8F4BB;
    border-radius: 50%;
    font-size: 90px;
    position: absolute;
    top: 0;
    right: 0;
    filter: drop-shadow(30px 10px 4px rgba(0, 0, 0, .80));
    height: 90px;
}

.contact-icon-lin:hover {
    color: white;
}

.contact-icon-git {
    background-color: #0EA86C;
    color: #B8F4BB;
    border-radius: 50%;
    font-size: 90px;
    position: absolute;
    top: 0;
    left: 0;
    filter: drop-shadow(30px 10px 4px rgba(0, 0, 0, .80));
    height: 90px;
}

.contact-icon-git:hover {
    color: white;
}

.panda-container {
    width: 325px;
    width: 600px;
    height: 500px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: translateY(50px);
}

@media screen and (max-width: 700px) {
    .contact-icon-lin {
        font-size: 70px;
        height: 70px;
    }
    .contact-icon-git {
        font-size: 70px;
        height: 70px;
    }
}

@media screen and (max-width: 400px) {
    .contact-icon-lin {
        font-size: 65x;
        height: 65px;
        filter: drop-shadow(10px 5px 4px rgba(0, 0, 0, .80));
    }
    .contact-icon-git {
        font-size: 65px;
        height: 65px;
        filter: drop-shadow(10px 5px 4px rgba(0, 0, 0, .80));
    }
}